body {
  background-image: url(../images/bg.jpg);
  background-position: left top;
  background-repeat: no-repeat;
  background-size: cover;

  ul,
  ol {
    list-style: none;
  }
}
.intup {
  display: inline-block;
  text-align: center;
  margin-left: 42%;
  margin-top: 30px;
}
.input-button:hover {
  color: white;
  background-color: royalblue;
  cursor: pointer;
}
.gallery {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 0;

  .photo-card {
    width: 350px;
    height: 350px;
    margin-bottom: 50px;
    border: 2px solid black;
    border-radius: 5px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
    background-color: white;

    .stats {
      display: flex;
      justify-content: space-evenly;

      .stats-item {
        margin: 5px auto;

        .material-icons:hover {
          color: blue;
          align-items: center;
          justify-self: center;
        }
      }
    }
  }
}
[data-big-image] {
  width: 346px;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}
.load-more {
  display: none;
  text-align: center;
  color: white;
  background-color: royalblue;
  margin-left: 35%;
  width: 500px;
  margin-bottom: 20px;

  &:hover {
    cursor: pointer;
  }
}
.is-open {
  display: inline-block;
}
